  <!---->
</template>

<script>
  import '../../static/css/footer.css'
  import '../../static/css/rest.css'
  import '../../static/js/font.js'
  import '../../static/js/bscroll.min.js'
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        num:'010-50961016'
      }
    },
    methods:{
      tel(){
        MessageBox({
          title:'',
          message:this.num,
          confirmButtonText:''
        }).then(action=>{
          window.location.href='tel://'+this.num
        });
