<script>
  import '../../static/css/example.css'
  export default{
    data(){
      return{
      }
    }
  }
  window.onload=function(){
    var aa = document.getElementById('aa');
    aa.onclick = function () {
      tab();
    }
    function tab(){
      var oAll=document.getElementById("whole_con");
      var aLis=oAll.getElementsByTagName("li");

      var oFen=document.getElementById("fen_con");
      var oLis=oFen.getElementsByTagName("li");

      for(var i=0;i<aLis.length;i++){
        aLis[i].index=i;
        aLis[i].onclick=function () {
          for(var j=0;j<aLis.length;j++){
            aLis[j].classList.remove("nav_color");
            oLis[j].classList.remove("right_show");
          }
          this.classList.add("nav_color");
          oLis[this.index].classList.add("right_show");
        }
      }
    };

  }

</script>

