<template>
  <div>
    <div>
      <img class="pic_banner" src="../assets/img/images_dynamics/news_banner.jpg" width="100%">
      <ul class="kc_part" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <li v-for="aa in kc1">
          <div class="pic">
            <h4><span>{{aa.month}}</span><span>{{aa.time}}</span></h4>
            <p>{{aa.year}}</p>
          </div>
          <div class="text">
            <h3>{{aa.title}}</h3>
            <p>{{aa.text}}</p>
          </div>
        </li>
        <mt-spinner type="fading-circle" color="#26a2ff" :size="40" ></mt-spinner>
      </ul>
    </div>
  </div>
</template>
<script>
  import '../../static/css/dynamics.css'
  export default {
    data(){
      return{
        num:1,
        kc1:[
          {title:'',month:'Sep',time:'22',year:'2017',text:'....'},
          {title:' ',month:'Sep',time:'15',year:'2017',text:''},
          {title:' ',month:'Sep',time:'11',year:'2017',text:''},
          {title:'',month:'Sep',time:'1',year:'2017',text:'831....'}
        ],
        kc2:[
          {title:'',month:'Aug',time:'25',year:'2017',text:'....'},
          {title:':',month:'Aug',time:'21',year:'2017',text:'817....'},
          {title:'',month:'Jul',time:'24',year:'2017',text:'2017....'},
          {title:'2017 ',month:'Jul',time:'7',year:'2017',text:'20172017....'}

        ],
        kc3:[
          {title:'',month:'May',time:'19',year:'2017',text:'201755....'},
          {title:'[1]',month:'May',time:'13',year:'2017',text:'....'},
          {title:'',month:'Mar',time:'23',year:'2017',text:'....'},
          {title:'USB++',month:'Mar',time:'13',year:'2017',text:'....'}
        ]
      }
    },
    methods:{
      loadMore() {
        this.loading = true;
        this.num++;
        setTimeout(() => {
          if(this.num==2){
            this.kc1=this.kc1.concat(this.kc2);
            this.loading = false;
          }
          else if(this.num==3){
            this.kc1=this.kc1.concat(this.kc3);
            this.loading = false;
          }
        }, 2000);
      }
    }
  }
</script>
<style>
</style>



