<template>
  <div class="auditions">
    <mt-field label="" placeholder="" v-model="username"></mt-field>
    <mt-field label="" placeholder="" type="number" v-model="phone"></mt-field>
    <mt-field label="qq" placeholder="qq" type="number" v-model="qq"></mt-field>
    <mt-radio
      title=""
      v-model="company"
      :options="['', '', '']">
    </mt-radio>
    <div v-show="off">{{array}}</div>
    <mt-button @click="sub" type="danger"></mt-button>

  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        off:false,
        username:'',
        phone:'',
        qq:'',
        company:'',
        array:[]
      }
    },
    methods:{
      sub(){
        if(this.username && this.phone && this.qq && this.company){
          this.off=true;
          this.array.unshift({
            username:this.username,
            phone:this.phone,
            qq:this.qq,
            company:this.company
          });

          MessageBox.alert('').then(action => {
            this.$router.push('/home')
          });
        }else{
          this.off=false;
          MessageBox.alert('');
        }

      }
    }
  }
</script>
<style>
  .mint-field,.mint-radiolist{
    text-align: left;
    margin-left:1.3333333rem;
  }
  .mint-msgbox-header{
    display: none;
  }
  .mint-msgbox .mint-msgbox-content .mint-msgbox-message{
    font-size:1.8rem;
  }
  .mint-button--danger{
    background: #133562;
    margin-top:1rem;
  }
</style>



