<template>
  <div id="app">
    <HeaderTop></HeaderTop>
    <transition name="animate"><router-view class="content" keep-alive></router-view></transition>
    <FooterEnd></FooterEnd>
  </div>
</template>

<script>
  import HeaderTop from './components/HeaderTop'
  import FooterEnd from './components/FooterEnd'
  export default {
    components:{HeaderTop,FooterEnd}
  }
</script>

<style>
  .content{ transition: 0.3s; position: fixed; width: 100%; top:5.9333333333rem; bottom:3.266666667rem;overflow-y:auto}
  .animate-enter{transform:translate(100%,0);opacity: 0}
  .animate-leave-active{transform:translate(-100%,0);opacity: 0}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



