<template>
  <div>
    <h3 class="new_title" v-html="titler"></h3>
    <p class="new_time">:<span  v-html="timely"></span></p>
    <p class="new_data" v-html="content"></p>

    <div  class="page" @click="last" v-if="index1>0">{{this.$route.params.list1[index1-1].title}}</div>
    <div  class="page1" v-else></div>
    <div class="page" @click="next" v-if="index1<this.$route.params.list1.length-1">{{this.$route.params.list1[index1+1].title}}</div>
    <div  class="page1" v-else></div>

    <div class="new_back" @click="back"></div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        index1:this.$route.params.index1,
        content:this.$route.params.list1[this.$route.params.index1].text,
        titler:this.$route.params.list1[this.$route.params.index1].title,
        timely:this.$route.params.list1[this.$route.params.index1].time
      }
    },
    methods:{
      back(){
        this.$router.go(-1)
      },
      last(){
        if(this.index1>0){
          this.index1--;
          this.content=this.$route.params.list1[this.index1].text;
          this.titler=this.$route.params.list1[this.index1].title;
          this.timely=this.$route.params.list1[this.index1].time
        }
      },
      next(){
        if(this.index1<this.$route.params.list1.length-1){
          this.index1++;
          this.content=this.$route.params.list1[this.index1].text;
          this.titler=this.$route.params.list1[this.index1].title;
          this.timely=this.$route.params.list1[this.index1].time
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .page{
    margin:0.666666rem 2rem 0 2rem;
    font-size:0.8rem;
    color:#000;
    line-height:1.5rem;
    text-decoration:underline;
  }
  .page1{
    margin:0.666666rem 2rem 0 2rem;
    font-size:0.8rem;
    color:#133562;
  }
  .new_title{
    font-size:1rem;
    color:#333;
    text-align:center;
    margin:1rem 1rem 0 1rem;
  }
  .new_time{
    font-size:0.5rem;
    text-align:center;
    margin-top:0.5rem;
  }
  .new_data{
    font-size:0.95rem;
    text-align:left;
    margin:1rem 2rem 2rem 2rem;
    text-indent:2rem;
    line-height:2rem;
  }
  .new_back{
    width:3.33333rem;
    height:1.5555rem;
    line-height:1.5555555rem;
    margin:2rem auto 0;
    background:#133562;
    border-radius:0.3333rem;
    color:#fff;
  }
</style>



